import React, { useEffect, useState } from 'react';
import {
    Button,
    AppBar,
    Tab,
    Tabs,
    Box,
    Typography,
    TextField,
    Radio,
    RadioGroup,
    FormControl,
    FormControlLabel,
    FormLabel,
    Toolbar,
} from '@material-ui/core';

import Texto from './texto';
import Archivos from './archivos';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import ListaEncriptada from './listaEncriptada';

import { useSelector, useDispatch } from 'react-redux';
import { deleteSession } from './../../redux/user';
import { Redirect } from 'react-router-dom';
import Bitacora from '../bitacora';
function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

export default function Encriptador() {
    const [value, setValue] = React.useState(0);
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    //section:Para uso del redux y la sesion de usuario
    const userStore = useSelector((state) => state.user.session);
    const dispatch = useDispatch();

    const cerrarSesion = () => {
        dispatch(deleteSession());
    };

    return (
        <div class="container">
            {userStore == null && <Redirect to="/" />}
            <div class="menu-izquierdo">
                <AppBar position="static">
                    <Toolbar>
                        <Typography className="grow-1" variant="h5" noWrap>
                            Crypto Unach
                        </Typography>
                        <Bitacora />
                        <Button
                            onClick={cerrarSesion}
                            endIcon={<ExitToAppIcon />}
                            className="cerrar-sesion"
                            edge="end"
                            color="inherit"
                        >
                            Cerrar sesion
                        </Button>
                    </Toolbar>
                    <Tabs value={value} onChange={handleChange} aria-label="simple tabs example" variant="fullWidth">
                        <Tab label="Texto" />
                        <Tab label="Archivos" />
                    </Tabs>
                </AppBar>
                <TabPanel value={value} index={0}>
                    <Texto></Texto>
                </TabPanel>

                <TabPanel value={value} index={1}>
                    <Archivos></Archivos>
                </TabPanel>
            </div>
            <ListaEncriptada />
        </div>
    );
}
