import React, { useEffect, useState } from 'react';
import {
    Button,
    TextField,
    Radio,
    RadioGroup,
    FormControl,
    FormControlLabel,
    FormLabel,
    FormHelperText,
} from '@material-ui/core';

import firebase, { firestore } from './../../firebase';
import Crypter from './../../util/crypter';

import _ from 'lodash';

import { useSelector } from 'react-redux';

export default function Texto() {
    const [nombre, setNombre] = useState('');
    const [contenido, setContenido] = useState('');
    const [tipo, setTipo] = useState(null);

    const userStore = useSelector((state) => state.user.session);
    const [errorNombre, setErrorNombre] = useState(false);
    const [errorContenido, setErrorContenido] = useState(false);
    const [errorTipo, setErrorTipo] = useState(false);

    const encriptar = () => {
        if (validar()) {
            firestore
                .collection('data')
                .doc()
                .set({
                    tipo: 'texto',
                    nombre: Crypter.getEncrypted(tipo.trim(), nombre),
                    contenido: Crypter.getEncrypted(tipo.trim(), contenido),
                    fecha: firebase.firestore.Timestamp.now(),
                    tipoEncriptacion: tipo,
                });
            setBitacora();
            setNombre('');
            setContenido('');
            setTipo(null);
        }
    };

    const validar = () => {
        if (_.isEmpty(nombre)) {
            setErrorNombre(true);
            return false;
        } else {
            setErrorNombre(false);
        }
        if (_.isEmpty(contenido)) {
            setErrorContenido(true);
            return false;
        } else {
            setErrorContenido(false);
        }
        if (_.isNull(tipo)) {
            setErrorTipo(true);
            return false;
        } else {
            setErrorTipo(false);
        }
        console.log(tipo);
        return true;
    };
    const setBitacora = () => {
        firestore
            .collection('bitacora')
            .doc()
            .set({
                fecha: firebase.firestore.Timestamp.now(),
                movimiento: Crypter.AESEncrypt(
                    'El usuario ' + Crypter.AESDecrypt(userStore.usuario) + ' hizo una encriptacion del archivo: ' + nombre
                ),
                tipoArchivo: Crypter.AESEncrypt('texto'),
                usuario: userStore.usuario,
            });
    };

    return (
        <div class="flex column">
            <TextField
                label="Nombre del archivo"
                variant="outlined"
                placeholder='Ingrese el nombre del archivo ej.: "Mensaje secreto"'
                value={nombre}
                onChange={(e) => setNombre(e.target.value)}
                helperText="Ingrese un nombre para su archivo"
                error={errorNombre}
            />
            <br />
            <TextField
                value={contenido}
                onChange={(e) => setContenido(e.target.value)}
                label="Ingrese el texto a encriptar"
                multiline
                rows={8}
                helperText="No puede dejar vacio el contenido"
                variant="outlined"
                error={errorContenido}
            />
            <br />
            <FormControl component="fieldset" error={errorTipo}>
                <FormLabel component="legend">Tipo de Cifrado</FormLabel>
                <RadioGroup aria-label="gender" name="gender1" value={tipo} onChange={(e) => setTipo(e.target.value)}>
                    <FormControlLabel value="DES" control={<Radio />} label="DES" />
                    <FormControlLabel value="RC4" control={<Radio />} label="RC4" />
                </RadioGroup>
                {errorTipo && <FormHelperText>Seleccione un tipo de encriptado</FormHelperText>}
            </FormControl>

            <Button color="primary" size="large" variant="contained" onClick={encriptar}>
                Encriptar
            </Button>
        </div>
    );
}
