import CryptoJS from 'crypto-js';

var Crypter = {
    getEncrypted(tipo, word) {
        switch (tipo) {
            case 'DES':
                return this.DESEncrypt(word);
            case 'RC4':
                return this.RC4Encrypt(word);
            default:
                return '';
        }
    },
    getDesencrypted(tipo, word) {
        switch (tipo) {
            case 'DES':
                return this.DESDecrypt(word);
            case 'RC4':
                return this.RC4Decrypt(word);
            default:
                return '';
        }
    },
    CryptoKey() {
        return CryptoJS.enc.Hex.parse('000102030405060708090a0b0c0d0e0f101112131415161718191a1b1c1d1e1f');
    },
    AESEncrypt(word) {
        return CryptoJS.AES.encrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString();
    },
    AESDecrypt(word) {
        return CryptoJS.AES.decrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString(CryptoJS.enc.Utf8);
    },
    DESEncrypt(word) {
        return CryptoJS.TripleDES.encrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString();
    },
    DESDecrypt(word) {
        return CryptoJS.TripleDES.decrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString(CryptoJS.enc.Utf8);
    },
    RC4Encrypt(word) {
        return CryptoJS.RC4.encrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString();
    },
    RC4Decrypt(word) {
        return CryptoJS.RC4.decrypt(word, this.CryptoKey(), { mode: CryptoJS.mode.ECB }).toString(CryptoJS.enc.Utf8);
    },
};
export default Crypter;
