import React, { useEffect, useState } from 'react';
import List from '@material-ui/core/List';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import TextFieldsIcon from '@material-ui/icons/TextFields';
import DescriptionIcon from '@material-ui/icons/Description';

import { firestore } from '../../firebase';
import Crypter from '../../util/crypter';
import moment from 'moment';

import { useDispatch } from 'react-redux';
import { setSelectedFile } from '../../redux/user';

import _ from 'lodash';

import Lottie from 'react-lottie';
import dev from './../../lottie/dev.json';

const defaultOptions = {
    loop: true,
    autoplay: true,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
    },
    animationData: dev,
};

export default function ListaEncriptada() {
    const [lista, setLista] = useState([]);

    useEffect(() => {
        var firtTime = true;
        var listaEscucha = [];
        var ref = firestore.collection('data').onSnapshot((response) => {
            var listaFinal = [];
            response.forEach((el) => {
                var object = el.data();
                object.id = el.id;
                if (!firtTime && !listaEscucha.some((lista) => lista.id === object.id)) {
                    object.new = true;
                }

                listaFinal.push(object);
            });
            listaFinal = listaFinal.sort((a, b) => {
                if (a.fecha.seconds < b.fecha.seconds) {
                    return 1;
                }
                if (a.fecha.seconds > b.fecha.seconds) {
                    return -1;
                }
                // a debe ser igual b
                return 0;
            });
            setLista(listaFinal);
            listaEscucha = listaFinal;
            firtTime = false;
        });
        console.log('lista', lista);
        return () => {
            ref();
        };
    }, []);

    const dispatch = useDispatch();
    const desencriptar = (doc) => {
        dispatch(setSelectedFile(doc));
    };
    useEffect(() => {
        console.log('lista', lista);
    }, [lista]);

    return (
        <div class="menu-derecho">
            <div className="flex column justify-center">
                {!_.isEmpty(lista) && (
                    <List component="nav" aria-label="main mailbox folders">
                        {lista.map((objeto) => {
                            var icono = '';
                            if (objeto.tipo === 'documento') {
                                icono = <DescriptionIcon />;
                            } else {
                                icono = <TextFieldsIcon />;
                            }

                            var nombre = Crypter.getDesencrypted(objeto.tipoEncriptacion, objeto.nombre);

                            return (
                                <ListItem
                                    button
                                    onClick={() => desencriptar(objeto)}
                                    className={objeto.new && 'new-item-list'}
                                >
                                    <ListItemIcon>{icono}</ListItemIcon>
                                    <ListItemText
                                        primary={nombre}
                                        secondary={moment.unix(objeto.fecha.seconds).format('LLLL')}
                                    />
                                </ListItem>
                            );
                        })}
                    </List>
                )}
                {_.isEmpty(lista) && (
                    <>
                        <h2>Aqui todavia no hay archivos :(</h2>
                        <Lottie width={380} options={defaultOptions} />
                    </>
                )}
            </div>
        </div>
    );
}
