import React, { useEffect, useState } from 'react';
import {
    Button,
    Radio,
    RadioGroup,
    FormControl,
    FormControlLabel,
    FormLabel,
    FormHelperText,
    LinearProgress,
} from '@material-ui/core';

import _ from 'lodash';
import firebase, { firestore, storage } from './../../firebase';
import Crypter from './../../util/crypter';
import FileManager from './../../util/fileManager';

import { useSelector } from 'react-redux';
export default function Archivos() {
    const [contenido, setContenido] = useState(null);
    const [tipo, setTipo] = useState(null);

    const [errorContenido, setErrorContenido] = useState(false);
    const [errorTipo, setErrorTipo] = useState(false);

    const [uploading, setUploading] = useState(false);
    const [progress, setProgress] = useState(0);
    const [textProgress, setTextProgress] = useState('');

    const userStore = useSelector((state) => state.user.session);

    const encriptar = () => {
        if (validar()) {
            setUploading(true);
            setTextProgress('Encriptando el archivo...');
            var reader = new FileReader();
            reader.readAsDataURL(contenido);
            reader.onload = (e) => {
                var text = e.target.result.split('base64,');
                text = text[1];
                text = Crypter.getEncrypted(tipo, text);
                var fileName = contenido.name;
                var typeFile = contenido.type;
                var blob = FileManager.base64toBlob(text, typeFile);

                var ref = storage.ref().child(fileName);

                var upload = ref.put(blob);

                upload.on(
                    'state_changed',
                    function (snapshot) {
                        // Observe state change events such as progress, pause, and resume
                        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                        setTextProgress('¡Archivo encriptado correctamente!. Subiendio archivo...');
                        var onProgress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                        setProgress(onProgress);
                    },
                    function (error) {
                        // Handle unsuccessful uploads
                    },
                    function () {
                        // Handle successful uploads on complete
                        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
                        upload.snapshot.ref.getDownloadURL().then(function (url) {
                            setTextProgress(
                                <span>
                                    ¡Completado! <br /> El archivo fue encriptado y subido correctamente al servidor.
                                </span>
                            );
                            firestore
                                .collection('data')
                                .doc()
                                .set({
                                    tipo: 'documento',
                                    nombre: Crypter.getEncrypted(tipo.trim(), fileName),
                                    contenido: Crypter.getEncrypted(tipo.trim(), url),
                                    fecha: firebase.firestore.Timestamp.now(),
                                    tipoEncriptacion: tipo,
                                })
                                .then(() => {
                                    setContenido(null);
                                    setTipo(null);
                                });
                            setBitacora();
                        });
                    }
                );
            };
        }
    };
    const setBitacora = () => {
        firestore
            .collection('bitacora')
            .doc()
            .set({
                fecha: firebase.firestore.Timestamp.now(),
                movimiento: Crypter.AESEncrypt(
                    'El usuario ' +
                        Crypter.AESDecrypt(userStore.usuario) +
                        ' hizo una encriptacion del archivo: ' +
                        contenido.name
                ),
                tipoArchivo: Crypter.AESEncrypt('documento'),
                usuario: userStore.usuario,
            });
    };

    const validar = () => {
        if (_.isNull(contenido)) {
            setErrorContenido(true);
            return false;
        } else {
            setErrorContenido(false);
        }
        if (_.isNull(tipo)) {
            setErrorTipo(true);
            return false;
        } else {
            setErrorTipo(false);
        }
        console.log(tipo);
        return true;
    };
    return (
        <div class="flex column">
            {!uploading && (
                <>
                    <FormControl error={errorContenido}>
                        <label>Archivo seleccionado: {contenido !== null && contenido.name}</label>
                        <input
                            hidden
                            id="contained-button-file"
                            type="file"
                            onChange={(e) => setContenido(e.target.files[0])}
                        />
                        <label htmlFor="contained-button-file">
                            <Button fullWidth variant="contained" color="primary" component="span">
                                Upload
                            </Button>
                        </label>
                        {errorContenido && <FormHelperText>Seleccione un archivo para encriptar</FormHelperText>}
                    </FormControl>
                    <br />
                    <FormControl component="fieldset" error={errorTipo}>
                        <FormLabel component="legend">Tipo de Cifrado</FormLabel>
                        <RadioGroup
                            aria-label="gender"
                            name="gender1"
                            value={tipo}
                            onChange={(e) => setTipo(e.target.value)}
                        >
                            <FormControlLabel value="DES" control={<Radio />} label="DES" />
                            <FormControlLabel value="RC4" control={<Radio />} label="RC4" />
                        </RadioGroup>
                        {errorTipo && <FormHelperText>Seleccione un tipo de encriptado</FormHelperText>}
                    </FormControl>
                    <br />

                    <Button color="primary" size="large" variant="contained" onClick={encriptar}>
                        Encriptar
                    </Button>
                </>
            )}

            {uploading && (
                <>
                    <h4 className="progress-text">{textProgress}</h4>
                    <LinearProgress variant="determinate" color="secondary" value={progress} />
                    {progress == 100 && (
                        <Button color="primary" size="large" variant="contained" onClick={() => setUploading(false)}>
                            Encriptar otro archivo
                        </Button>
                    )}
                </>
            )}
        </div>
    );
}
