//CONSTANTES
const defaultState = {
    session: sessionStorage.session !== undefined ? JSON.parse(sessionStorage.getItem('session')) : null,
    selectedFile: null,
};

//TYPES
const SET_SESSION = 'SET_SESSION';
const SET_SELECTED_FILE = 'SET_SELECTED_FILE';

//REDUCER
export default function userReducer(state = defaultState, action) {
    switch (action.type) {
        case SET_SESSION:
            return { ...state, session: action.payload };
        case SET_SELECTED_FILE:
            return { ...state, selectedFile: action.payload };
        default:
            return state;
    }
}

//ACCIONES
export const setSesion = (user) => (dispatch, getState) => {
    sessionStorage.setItem('session', JSON.stringify(user));
    dispatch({
        type: SET_SESSION,
        payload: user,
    });
};

export const setSelectedFile = (doc) => (dispatch, getState) => {
    dispatch({
        type: SET_SELECTED_FILE,
        payload: doc,
    });
};

export const deleteSession = () => (dispatch, getState) => {
    sessionStorage.removeItem('session');
    dispatch({
        type: SET_SESSION,
        payload: null,
    });
};
