import React, { createElement, useEffect, useState } from 'react';
import { Button, AppBar, Box, Typography, Toolbar, LinearProgress, TextField } from '@material-ui/core';
import LockIcon from '@material-ui/icons/Lock';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import ListaEncriptada from './listaEncriptada';
import _ from 'lodash';

import { useSelector, useDispatch } from 'react-redux';
import { deleteSession } from '../../redux/user';
import { Redirect } from 'react-router-dom';

import Crypter from './../../util/crypter';
import FileManager from './../../util/fileManager';
import firebase, { firestore } from './../../firebase';
import Bitacora from '../bitacora';

export default function Desencriptador() {
    const [file, setFile] = useState({});
    const [isEncrypted, setIsEncrypted] = useState(true);
    const [linkArchivo, setLinkArchivo] = useState('');
    const [isDocument, setIsDocument] = useState(false);

    const [textoDesencriptado, setTextoDesencriptado] = useState('');

    const [isDecrypting, setIsDecrypting] = useState(false);
    const [textProgress, setTextProgress] = useState('');

    //section:Para uso del redux y la sesion de usuario
    const userStore = useSelector((state) => state.user.session);
    const selectedFile = useSelector((state) => state.user.selectedFile);
    const dispatch = useDispatch();

    useEffect(() => {
        if (selectedFile !== null) {
            console.log(selectedFile);
            setFile(selectedFile);
            if (selectedFile.tipo == 'documento') {
                setIsDocument(true);
            } else {
                setIsDocument(false);
            }
            setIsEncrypted(true);
        }
    }, [selectedFile]);

    const desencriptar = () => {
        if (file.tipo == 'documento') {
            setIsDecrypting(true);
            setTextProgress('Descargando archivo...');
            fetch(Crypter.getDesencrypted(file.tipoEncriptacion, file.contenido))
                .then((resp) => resp.blob())
                .then((originalFile) => {
                    setTextProgress('Desencriptando archivo...');
                    var reader = new FileReader();
                    reader.readAsDataURL(originalFile);
                    console.log(originalFile);
                    reader.onload = (e) => {
                        var text = e.target.result.split('base64,').pop();

                        var typeFile = originalFile.type;
                        var extension = typeFile.split('/').pop();

                        text = Crypter.getDesencrypted(file.tipoEncriptacion, text);
                        var fileName = Crypter.getDesencrypted(file.tipoEncriptacion, file.nombre);

                        var blobDecrypted = FileManager.base64toBlob(text, typeFile);
                        var url = window.URL.createObjectURL(blobDecrypted);

                        // url += '.' + extension;

                        console.log(extension);

                        var link = (
                            <Button variant="contained" color="primary" href={url} download={fileName}>
                                Descargar archivo
                            </Button>
                        );
                        setLinkArchivo(link);
                        setIsDecrypting(false);
                        setIsEncrypted(false);
                        setBitacora();
                    };
                });
        } else {
            setTextoDesencriptado(Crypter.getDesencrypted(file.tipoEncriptacion, file.contenido));
            setIsEncrypted(false);
            setBitacora();
        }
    };

    const setBitacora = () => {
        firestore
            .collection('bitacora')
            .doc()
            .set({
                fecha: firebase.firestore.Timestamp.now(),
                movimiento: Crypter.AESEncrypt(
                    'El usuario ' +
                        Crypter.AESDecrypt(userStore.usuario) +
                        ' hizo una desencriptacion del archivo: ' +
                        Crypter.getDesencrypted(file.tipoEncriptacion, file.nombre)
                ),
                tipoArchivo: Crypter.AESEncrypt(file.tipo),
                usuario: userStore.usuario,
            });
    };

    const cerrarSesion = () => {
        dispatch(deleteSession());
    };

    return (
        <div class="container">
            {userStore == null && <Redirect to="/" />}
            <div class="menu-izquierdo">
                <AppBar position="relative">
                    <Toolbar>
                        <Typography className="grow-1" variant="h5" noWrap>
                            Crypto Unach
                        </Typography>
                        <Bitacora />
                        <Button
                            onClick={cerrarSesion}
                            endIcon={<ExitToAppIcon />}
                            className="cerrar-sesion"
                            edge="end"
                            color="inherit"
                        >
                            Cerrar sesion
                        </Button>
                    </Toolbar>
                </AppBar>
                <div className="flex column center height-complete menu-desencriptado">
                    {!_.isEmpty(file) && (
                        <div className="flex column center" style={{ width: '80%' }}>
                            <h2>{Crypter.getDesencrypted(file.tipoEncriptacion, file.nombre)}</h2>
                            {isEncrypted && (
                                <>
                                    <LockIcon style={{ fontSize: 100, color: '#607d8b' }} />
                                    <h3>Encriptado con {file.tipoEncriptacion}</h3>
                                    <Button color="primary" variant="contained" onClick={desencriptar}>
                                        Desencriptar
                                    </Button>
                                </>
                            )}
                            {isDecrypting && (
                                <>
                                    <h4 className="progress-text">{textProgress}</h4>
                                    <LinearProgress color="secondary" />
                                </>
                            )}
                            {isDocument && !isEncrypted && (
                                <>
                                    <LockOpenIcon style={{ fontSize: 100, color: '#4caf50' }} />
                                    <h3 className="progress-text">
                                        ¡Listo!. <br /> Archivo desencriptado exitosamente
                                    </h3>
                                    {linkArchivo}
                                </>
                            )}
                            {!isDocument && !isEncrypted && (
                                <>
                                    <LockOpenIcon style={{ fontSize: 100, color: '#4caf50' }} />
                                    <h3 className="progress-text">
                                        ¡Listo!. <br /> Archivo desencriptado exitosamente
                                    </h3>
                                    <TextField
                                        id="filled-basic"
                                        label="Filled"
                                        variant="filled"
                                        multiline={true}
                                        rows={15}
                                        value={textoDesencriptado}
                                        style={{ alignSelf: 'normal' }}
                                    />
                                </>
                            )}
                        </div>
                    )}

                    {_.isEmpty(file) && <h2>Seleccione un archivo de la lista para desencriptar</h2>}
                </div>
            </div>
            <ListaEncriptada />
        </div>
    );
}
