import React, { useState, useEffect } from 'react';
import { Modal, IconButton } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import moment from 'moment';

import Crypter from './../util/crypter';

import AssignmentIcon from '@material-ui/icons/Assignment';

import { firestore } from './../firebase';

export default function Bitacora() {
    const [open, setOpen] = useState(false);
    function createData(name, tipo, usuario, fecha) {
        return { name, tipo, usuario, fecha };
    }
    const [rows, setRows] = useState([]);

    useEffect(() => {
        if (open) {
            firestore
                .collection('bitacora')
                .get()
                .then((data) => {
                    var finalRow = [];
                    data.forEach((element) => {
                        var e = element.data();
                        finalRow.push(
                            createData(
                                Crypter.AESDecrypt(e.movimiento),
                                Crypter.AESDecrypt(e.tipoArchivo),
                                Crypter.AESDecrypt(e.usuario),
                                e.fecha.seconds
                            )
                        );
                    });
                    finalRow = finalRow.sort((a, b) => {
                        if (a.fecha < b.fecha) {
                            return 1;
                        }
                        if (a.fecha > b.fecha) {
                            return -1;
                        }
                        // a debe ser igual b
                        return 0;
                    });
                    console.log(finalRow);
                    setRows(finalRow);
                });
        }
    }, [open]);
    return (
        <>
            <Modal open={open} onClose={() => setOpen(false)} className="flex center modal">
                <TableContainer component={Paper} color="primary" className="bitacora">
                    <Table stickyHeader aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">Descripción</TableCell>
                                <TableCell align="center">Tipo archivo</TableCell>
                                <TableCell align="center">Usuario</TableCell>
                                <TableCell align="center">Fecha</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row) => (
                                <TableRow key={row.name}>
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="right">{row.tipo}</TableCell>
                                    <TableCell align="right">{row.usuario}</TableCell>
                                    <TableCell align="right">{moment.unix(row.fecha).format('LLLL')}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Modal>
            <IconButton onClick={() => setOpen(true)}>
                <AssignmentIcon />
            </IconButton>
        </>
    );
}
