import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

// IMPORTACION DE COMPONTENTES/RUTAS

import Login from './pages/login';
import Encriptador from './pages/encriptador/encriptador';
import Desencriptador from './pages/desencriptador/desencriptador';
export default class Router extends Component {
    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <Route exact path="/" component={Login} />
                    <Route exact path="/encriptador" component={Encriptador} />
                    <Route exact path="/desencriptador" component={Desencriptador} />
                </Switch>
            </BrowserRouter>
        );
    }
}
