import React, { useState } from 'react';
import {
    Button,
    TextField,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    CircularProgress,
    FormHelperText,
} from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import firebase, { firestore } from './../firebase';
import Crypter from './../util/crypter';

import { useSelector, useDispatch } from 'react-redux';
import { setSesion } from './../redux/user';
import { Redirect } from 'react-router-dom';
import _ from 'lodash';
export default function Login() {
    const [correo, setCorreo] = useState('');
    const [contraseña, setContraseña] = useState('');
    const [tipoUsuario, setTipoUsuario] = useState(null);
    const [registrarse, setRegistrarse] = useState(false);
    const [nombre, setNombre] = useState('');

    const [errorLogin, setErrorLogin] = useState(false);

    const [loading, setLoading] = useState(false);
    const [errorCorreo, seterrorCorreo] = useState(false);
    const [errorNombre, setErrorNombre] = useState(false);
    const [errorTipo, setErrorTipo] = useState(false);

    //section:Para uso del redux y la sesion de usuario
    const userStore = useSelector((state) => state.user.session);
    const dispatch = useDispatch();

    var iniciarSesion = () => {
        setLoading(true);
        firestore
            .collection('users')
            .where('usuario', '==', Crypter.AESEncrypt(correo))
            .where('password', '==', Crypter.AESEncrypt(contraseña))
            .get()
            .then((response) => {
                if (!response.empty) {
                    var user = response.docs[0].data();
                    dispatch(setSesion(user));
                } else {
                    setErrorLogin(true);
                }
                setLoading(false);
            });
    };
    var validate = () => {
        if (_.isEmpty(nombre)) {
            setErrorNombre(true);
            return false;
        } else {
            setErrorNombre(false);
        }

        if (_.isNull(tipoUsuario)) {
            setErrorTipo(true);
            return false;
        } else {
            setErrorTipo(false);
        }
        return true;
    };
    var handleRegistrarse = () => {
        var ref = firestore.collection('users');
        ref.where('usuario', '==', Crypter.AESEncrypt(correo))
            .get()
            .then((response) => {
                if (response.empty) {
                    seterrorCorreo(false);
                    if (validate()) {
                        ref.doc().set({
                            usuario: Crypter.AESEncrypt(correo),
                            password: Crypter.AESEncrypt(contraseña),
                            fecha: firebase.firestore.Timestamp.now(),
                            tipo: tipoUsuario,
                            nombre: Crypter.AESEncrypt(nombre),
                        });
                    }
                } else {
                    seterrorCorreo(true);
                }
            });
    };

    return (
        <div class="container">
            {userStore != null ? (
                userStore.tipo == 'encriptador' ? (
                    <Redirect to="/encriptador" />
                ) : (
                    <Redirect to="/desencriptador" />
                )
            ) : (
                <Redirect to="/" />
            )}
            {!registrarse && (
                <form class="login-wrap">
                    <h1 class="color-black align-center">Inicio de Sesión</h1>
                    {errorLogin && (
                        <div className="error-login">
                            No se encontro este usuario o la contraseña es incorrecta. Por favor corriga los datos e
                            intentelo de nuevo.
                        </div>
                    )}

                    <div>
                        <div class="form-group">
                            <TextField
                                variant="outlined"
                                type="text"
                                name="correo"
                                label="Correo"
                                value={correo}
                                onChange={(e) => {
                                    setCorreo(e.target.value);
                                }}
                            />
                        </div>
                        <div class="form-group">
                            <TextField
                                variant="outlined"
                                type="password"
                                name="contraseña"
                                label="Contraseña"
                                value={contraseña}
                                onChange={(e) => {
                                    setContraseña(e.target.value);
                                }}
                            />
                        </div>
                    </div>
                    {loading && <CircularProgress color="secondary" />}

                    <div class="buttons-login">
                        <Button color="primary" size="large" variant="contained" onClick={iniciarSesion}>
                            Iniciar sesión
                        </Button>
                        <Button color="primary" size="large" variant="outlined" onClick={() => setRegistrarse(true)}>
                            Registrarse
                        </Button>
                    </div>
                </form>
            )}
            {registrarse && (
                <form class="login-wrap">
                    <h1 class="color-black align-center">Registrarse</h1>
                    <div>
                        <div class="form-group">
                            <TextField
                                variant="outlined"
                                type="text"
                                name="nombre"
                                label="Nombre"
                                value={nombre}
                                onChange={(e) => {
                                    setNombre(e.target.value);
                                }}
                                helperText={errorNombre && 'Por favor, proporcione sus nombre'}
                                error={errorNombre}
                            />
                        </div>
                        <div class="form-group">
                            <TextField
                                variant="outlined"
                                type="text"
                                name="correo"
                                label="Usuario"
                                value={correo}
                                onChange={(e) => {
                                    setCorreo(e.target.value);
                                }}
                                error={errorCorreo}
                                helperText={errorCorreo && 'Este nombre de usuario ya existe. Por favor utilice otro.'}
                            />
                        </div>
                        <div class="form-group">
                            <TextField
                                variant="outlined"
                                type="password"
                                name="contraseña"
                                label="Contraseña"
                                value={contraseña}
                                onChange={(e) => {
                                    setContraseña(e.target.value);
                                }}
                            />
                        </div>
                        <div class="form-group">
                            <FormControl fullWidth variant="outlined" error={errorTipo}>
                                <InputLabel>Tipo de usuario</InputLabel>
                                <Select
                                    label="Tipo de usuario"
                                    value={tipoUsuario}
                                    onChange={(e) => {
                                        setTipoUsuario(e.target.value);
                                    }}
                                    displayEmpty
                                >
                                    <MenuItem value="encriptador">Encriptador</MenuItem>
                                    <MenuItem value="desencriptador">Desencriptador</MenuItem>
                                </Select>
                                {errorTipo && <FormHelperText>Seleccione un tipo de usuario.</FormHelperText>}
                            </FormControl>
                        </div>
                    </div>
                    <div class="buttons-login">
                        <Button color="primary" size="large" variant="contained" onClick={handleRegistrarse}>
                            Registrarse
                        </Button>
                        <Button
                            color="primary"
                            size="large"
                            variant="outlined"
                            onClick={() => setRegistrarse(false)}
                            startIcon={<ArrowBackIosIcon />}
                        >
                            Regresar
                        </Button>
                    </div>
                </form>
            )}
        </div>
    );
}
